.trailsText {
    position: relative;
    width: 100%;
    height: 80px;
    line-height: 80px;
    color: #282c34;
    font-size: 6em;
    font-weight: 800;
    letter-spacing: -0.05em;
    will-change: transform, opacity;
    overflow: hidden;
  }
  
  .trailsText > div {
    padding-right: 0.05em;
    overflow: hidden;
  }
  
  .container {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
    width: 100%;
  }
  
  .draw-btns {
    display: flex;
    flex-direction: column;
  }

  .draw-close {
    width: 100%;
    display: flex;
    justify-content: end;
  }

  .trails {
    width: 100%;
  }
  
  .draw-btns {
    display: flex;
    flex-direction: column;
  }

  div > a {
    display: flex;
    justify-content: center;
  }

  .draw-links {
    text-align: center;
    align-self: center;
    color: white;
    font-weight: 600;
    font-size: x-large;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-shadow: 0 0 1px white;
  }

  .draw-close-con {
    display: flex;
    justify-content: end;
    padding: 20px;
  }

  .draw-close {
    color: white;
    font-size: large;
  }