.home-overlay {
  position: fixed ;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 11;
}

.home-logo {
  z-index: 100;
}

.home-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  margin-left: 0;
  margin-right: auto;
  position: relative;
  height: 100%;
  width: 100%;
  padding: 40px 60px 60px 60px;
  box-sizing: border-box;
}

.home-info {
  color: white;
  font-style: italic;
  font-weight: 600;
  font-size: 12px;
}

@media (min-width: 900px) {
  .home-container {
    width: 45%;
  }
}

.home-details {
  color: white;
  font-weight: 500;
}

.home-details>a {
  text-decoration: underline;
}

.home-details>a:hover {
  cursor: pointer;
}

.menuBurger {
  left: unset !important;
  right: 2vmax !important;
}

.linksReactNavbar {
  cursor: pointer;
  position: relative;
  padding: 10px 20px !important;
  background: transparent;
  font-size: 28px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  transition: all 1s;
}
.linksReactNavbar:after, .linksReactNavbar:before {
  content: " ";
  width: 10px;
  height: 10px;
  position: absolute;
  border: 0px solid #fff;
  transition: all 1s;
}
.linksReactNavbar:after {
  top: -1px;
  left: -1px;
  border-top: 5px solid white;
  border-left: 5px solid white;
}
.linksReactNavbar:before {
  bottom: -1px;
  right: -1px;
  border-bottom: 5px solid white;
  border-right: 5px solid white;
}
.linksReactNavbar:hover {
  border-top-right-radius: 0px;
  border-bottom-left-radius: 0px;
}
.linksReactNavbar:hover:before, .linksReactNavbar:hover:after {
  width: 100%;
  height: 100%;
}

.nav1 > .linksReactNavbar, .nav2 > .linksReactNavbar {
  display: none;
}