.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header-bar {
  display: flex;
  justify-content: center;
  top: 0;
  position: fixed;
  width: 100%;
  z-index: 10 !important;
  box-shadow: 0px 0px 10px 5px #87b6b5;
}


.visible {
  top: 0;
  transition: top 0.4s ease-out;
  
}

.hidden {
  top: -80px;
  transition: top 0.4s ease-out;
}


.draw-overlay {
  position: fixed;
  display: none;
  width: 100%; 
  height: 100%; 
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.9); 
  z-index: 11;
}

.draw-overlay > div {
  width: 100%;
}

.home-con {
  display: flex;
  justify-content: center;
  margin-top: 100px !important;
}

.contents-con {
  display: flex;
  justify-content: space-evenly;
  justify-content: row;
  margin-top: 30px !important;
}

.goal-container {
  border-top: 3px solid #282c34;
  border-bottom: 3px solid #282c34;
}

@media only screen and (max-width: 768px) {
  .contents-con {
    flex-direction: column;
    justify-content: center !important;
    align-items: center;
  }
}