.basic {
    width: 40%;
}

.basic > h2 {
    position: relative;
    font-size: 30px;
    z-index: 1;
    overflow: hidden;
    text-align: center;
    color: #282c34;
}

 .basic > h2::after, .basic > h2::before {
    position: absolute;
    top: 45%;
    overflow: hidden;
    width: 50%;
    height: 3px;
    content: '\a0';
    background-color: #282c34;
}

.basic > h2::before {
    margin-left: -50%;
    text-align: right;
}

.basic > p {
    color: #282c34;
}

.service > h2 {
    position: relative;
    font-size: 30px;
    z-index: 1;
    overflow: hidden;
    text-align: right;
    color: #282c34;
}

 .service > h2:before {
    position: absolute;
    top: 51%;
    overflow: hidden;
    width: 100%;
    height: 2px;
    content: '\a0';
    background-color: #282c34;
}

.service > p {
    color: #282c34;
}

.service > h2:before {
    margin-left: -100%;
    text-align: right;
}

.basic > div {
    display: flex;
    justify-content: center;
    height: 60px;
  }

  .basic > div > span{
    color: #282c34;
    font-size: 35px;
  }
  
  .basic-cards {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  

  .flow-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 400px !important;
  }

  .flow-sub {
    margin-bottom: 8px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 100% ;
  }

  .flow-sub > div {
    margin-bottom: 8px;
    width: 49%;
    height: 100%;
    border: 0.5px solid gray;
    display: flex;
    padding-right: 5px;
  }

  .flow-sub > div > p{
    width: 70%;
    font-size: 18px;
    margin-top: auto;
    margin-bottom: auto;
    padding: 20px;
  }

 

  @media only screen and (max-width: 768px) {
    .basic > h2 {
        font-size: 20px;
    }
    .basic > div {
        height: 40px;
    }
    .basic > div > span{
        color: #282c34;
        font-size: 27px;
    }
    .basic > p {
        text-align: center;
    }
    .basic {
        width: 80%;
    }
    .flow-sub {
        margin-bottom: 0px;
        display: flex;
        flex-direction: column;
    }
    .flow-sub > div {
        width: 100%;
      }
}