.links {
    text-align: center;
    align-self: center;
    margin-right: 50px !important;
    color: white;
    font-weight: 600;
    font-size: large;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.links:hover{
    cursor: pointer;
    color: white;
    text-shadow: 2px 2px 20px white;
}

.nav-bar {
    padding-right: 3px;
    display: flex;
}

.nav-btns {
    display: flex;
}

.logo-con {
    margin-left: 20px !important;
    padding-left: 2px;
    display: flex;
    justify-content: center;
}

.logo {
    display: flex;
    width: 120px;
    height: 57px;
    align-self: center;
}

.nav-menu {
    padding-right: 10px;
    display: none;
    font-size: large;
    align-items: center;
}

.header {
    position: fixed !important;
}

.menu-btn {
    font-size: large;
    color: white;
}

@media only screen and (max-width: 768px) {
    .nav-btns {
        display: none;
    }

    .nav-menu {
        display: flex;
    }
}